import React from 'react';
import {
  Box,
  Paper,
  Typography,
  Switch,
  FormControlLabel,
  Slider,
  Grid,
  Divider,
  Collapse,
  useTheme,
} from '@mui/material';
import { Settings, TuneOutlined } from '@mui/icons-material';

interface ThresholdControlsProps {
  isGroupingEnabled: boolean;
  stabilityThreshold: number;
  transitionThreshold: number;
  silenceThreshold: number;
  onGroupingToggle: (enabled: boolean) => void;
  onStabilityChange: (value: number) => void;
  onTransitionChange: (value: number) => void;
  onSilenceChange: (value: number) => void;
}

const ThresholdControls: React.FC<ThresholdControlsProps> = ({
  isGroupingEnabled,
  stabilityThreshold,
  transitionThreshold,
  silenceThreshold,
  onGroupingToggle,
  onStabilityChange,
  onTransitionChange,
  onSilenceChange,
}) => {
  const theme = useTheme();
  const isDark = theme.palette.mode === 'dark';

  const sliderSx = {
    color: theme.palette.primary.main,
    height: 6,
    '& .MuiSlider-track': {
      background: `linear-gradient(90deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
      border: 'none',
    },
    '& .MuiSlider-thumb': {
      width: 16,
      height: 16,
      background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
      boxShadow: `0 0 8px ${theme.palette.primary.main}50`,
      '&:hover, &.Mui-focusVisible': {
        boxShadow: `0 0 12px ${theme.palette.primary.main}70`,
      },
    },
    '& .MuiSlider-rail': {
      backgroundColor: isDark ? 'rgba(255,255,255,0.15)' : 'rgba(0,0,0,0.1)',
    },
    '& .MuiSlider-mark': {
      backgroundColor: isDark ? 'rgba(255,255,255,0.3)' : 'rgba(0,0,0,0.2)',
    },
    '& .MuiSlider-markLabel': {
      fontSize: '0.7rem',
      color: 'text.secondary',
    },
  };

  return (
    <Paper 
      elevation={0} 
      sx={{ 
        p: 3, 
        mb: 3,
        background: isDark 
          ? 'linear-gradient(145deg, rgba(27,38,59,0.9) 0%, rgba(13,27,42,0.95) 100%)'
          : 'linear-gradient(145deg, rgba(255,255,255,0.95) 0%, rgba(255,248,240,0.9) 100%)',
        backdropFilter: 'blur(10px)',
        border: '1px solid',
        borderColor: isDark ? 'rgba(255,107,53,0.2)' : 'rgba(123,45,142,0.15)',
        borderRadius: 3,
      }}
    >
      <Box display="flex" alignItems="center" gap={1.5} mb={2}>
        <Box
          sx={{
            width: 36,
            height: 36,
            borderRadius: '50%',
            background: `linear-gradient(135deg, ${theme.palette.primary.main}30 0%, ${theme.palette.secondary.main}30 100%)`,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          <TuneOutlined sx={{ fontSize: 20, color: theme.palette.primary.main }} />
        </Box>
        <Typography 
          variant="h6"
          sx={{
            background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
            backgroundClip: 'text',
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent',
            fontWeight: 600,
          }}
        >
          Note Grouping Controls
        </Typography>
      </Box>
      
      <Box mb={2}>
        <FormControlLabel
          control={
            <Switch
              checked={isGroupingEnabled}
              onChange={(e) => onGroupingToggle(e.target.checked)}
              sx={{
                '& .MuiSwitch-switchBase.Mui-checked': {
                  color: theme.palette.primary.main,
                  '& + .MuiSwitch-track': {
                    background: `linear-gradient(90deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
                    opacity: 1,
                  },
                },
                '& .MuiSwitch-track': {
                  backgroundColor: isDark ? 'rgba(255,255,255,0.2)' : 'rgba(0,0,0,0.2)',
                },
              }}
            />
          }
          label={
            <Box>
              <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                Enable Note Grouping
              </Typography>
              <Typography variant="body2" color="text.secondary" sx={{ opacity: 0.8 }}>
                {isGroupingEnabled 
                  ? 'Notes will be grouped and filtered based on thresholds below'
                  : 'All detected notes will be displayed in real-time'
                }
              </Typography>
            </Box>
          }
        />
      </Box>

      <Collapse in={isGroupingEnabled}>
        <Divider 
          sx={{ 
            mb: 3,
            borderColor: isDark ? 'rgba(255,107,53,0.2)' : 'rgba(123,45,142,0.1)',
          }} 
        />
        
        <Typography 
          variant="subtitle2" 
          gutterBottom 
          color="text.secondary"
          sx={{ 
            fontWeight: 500,
            display: 'flex',
            alignItems: 'center',
            gap: 1,
          }}
        >
          <Settings sx={{ fontSize: 16 }} />
          Threshold Settings (in milliseconds)
        </Typography>
        
        <Grid container spacing={3} sx={{ mt: 1 }}>
          <Grid item xs={12} md={4}>
            <Box
              sx={{
                p: 2,
                borderRadius: 2,
                backgroundColor: isDark ? 'rgba(255,107,53,0.05)' : 'rgba(255,107,53,0.03)',
                border: '1px solid',
                borderColor: isDark ? 'rgba(255,107,53,0.15)' : 'rgba(255,107,53,0.1)',
              }}
            >
              <Typography variant="body2" gutterBottom sx={{ fontWeight: 600, color: theme.palette.primary.main }}>
                Stability: {stabilityThreshold}ms
              </Typography>
              <Typography variant="caption" color="text.secondary" display="block" gutterBottom sx={{ mb: 2 }}>
                Minimum time a note must be held to be considered stable
              </Typography>
              <Slider
                value={stabilityThreshold}
                onChange={(_, value) => onStabilityChange(value as number)}
                min={10}
                max={500}
                step={10}
                marks={[
                  { value: 50, label: '50' },
                  { value: 200, label: '200' },
                  { value: 500, label: '500' },
                ]}
                valueLabelDisplay="auto"
                valueLabelFormat={(value) => `${value}ms`}
                sx={sliderSx}
              />
            </Box>
          </Grid>
          
          <Grid item xs={12} md={4}>
            <Box
              sx={{
                p: 2,
                borderRadius: 2,
                backgroundColor: isDark ? 'rgba(123,45,142,0.05)' : 'rgba(123,45,142,0.03)',
                border: '1px solid',
                borderColor: isDark ? 'rgba(123,45,142,0.15)' : 'rgba(123,45,142,0.1)',
              }}
            >
              <Typography variant="body2" gutterBottom sx={{ fontWeight: 600, color: theme.palette.secondary.main }}>
                Transition: {transitionThreshold}ms
              </Typography>
              <Typography variant="caption" color="text.secondary" display="block" gutterBottom sx={{ mb: 2 }}>
                Minimum time for a new note to be committed after a change
              </Typography>
              <Slider
                value={transitionThreshold}
                onChange={(_, value) => onTransitionChange(value as number)}
                min={10}
                max={200}
                step={5}
                marks={[
                  { value: 25, label: '25' },
                  { value: 100, label: '100' },
                  { value: 200, label: '200' },
                ]}
                valueLabelDisplay="auto"
                valueLabelFormat={(value) => `${value}ms`}
                sx={sliderSx}
              />
            </Box>
          </Grid>
          
          <Grid item xs={12} md={4}>
            <Box
              sx={{
                p: 2,
                borderRadius: 2,
                backgroundColor: isDark ? 'rgba(255,179,71,0.05)' : 'rgba(255,179,71,0.03)',
                border: '1px solid',
                borderColor: isDark ? 'rgba(255,179,71,0.15)' : 'rgba(255,179,71,0.1)',
              }}
            >
              <Typography variant="body2" gutterBottom sx={{ fontWeight: 600, color: '#FFB347' }}>
                Silence: {silenceThreshold}ms
              </Typography>
              <Typography variant="caption" color="text.secondary" display="block" gutterBottom sx={{ mb: 2 }}>
                Minimum silence duration to create a break between notes
              </Typography>
              <Slider
                value={silenceThreshold}
                onChange={(_, value) => onSilenceChange(value as number)}
                min={50}
                max={1000}
                step={25}
                marks={[
                  { value: 100, label: '100' },
                  { value: 500, label: '500' },
                  { value: 1000, label: '1s' },
                ]}
                valueLabelDisplay="auto"
                valueLabelFormat={(value) => `${value}ms`}
                sx={sliderSx}
              />
            </Box>
          </Grid>
        </Grid>
        
        <Box 
          mt={3} 
          p={2} 
          sx={{ 
            background: `linear-gradient(135deg, ${theme.palette.primary.main}08 0%, ${theme.palette.secondary.main}08 100%)`,
            borderRadius: 2,
            border: '1px solid',
            borderColor: isDark ? 'rgba(255,107,53,0.15)' : 'rgba(123,45,142,0.1)',
          }}
        >
          <Typography variant="body2" color="text.secondary" sx={{ lineHeight: 1.7 }}>
            <strong style={{ color: theme.palette.primary.main }}>How it works:</strong> When grouping is enabled, the system will wait for notes to be stable 
            for the specified duration before displaying them. This helps reduce noise and provides cleaner output, 
            but may miss very quick transitions. Disable grouping for real-time detection of all notes.
          </Typography>
        </Box>
      </Collapse>
    </Paper>
  );
};

export default ThresholdControls;
