import { SwaraNote, DetectedNote, RawFrequencyData } from '../types';

// Standard 12 semitone swara mapping
export const SWARA_NOTES: SwaraNote[] = [
  { name: 'Sa', symbol: 'S', frequency: 0, semitones: 0 },
  { name: 'Rishabham 1', symbol: 'R1', frequency: 0, semitones: 1 },
  { name: 'Rishabham 2', symbol: 'R2', frequency: 0, semitones: 2 },
  { name: 'Gandharam 2', symbol: 'G2', frequency: 0, semitones: 3 },
  { name: 'Gandharam 3', symbol: 'G3', frequency: 0, semitones: 4 },
  { name: 'Madhyamam 1', symbol: 'M1', frequency: 0, semitones: 5 },
  { name: 'Madhyamam 2', symbol: 'M2', frequency: 0, semitones: 6 },
  { name: 'Panchamam', symbol: 'P', frequency: 0, semitones: 7 },
  { name: 'Dhaivatam 1', symbol: 'D1', frequency: 0, semitones: 8 },
  { name: 'Dhaivatam 2', symbol: 'D2', frequency: 0, semitones: 9 },
  { name: 'Nishadam 2', symbol: 'N2', frequency: 0, semitones: 10 },
  { name: 'Nishadam 3', symbol: 'N3', frequency: 0, semitones: 11 },
];

// Standard note frequencies (A4 = 440Hz)
export const NOTE_FREQUENCIES: { [key: string]: number } = {
  'C': 261.63,
  'C#': 277.18,
  'D': 293.66,
  'D#': 311.13,
  'E': 329.63,
  'F': 349.23,
  'F#': 369.99,
  'G': 392.00,
  'G#': 415.30,
  'A': 440.00,
  'A#': 466.16,
  'B': 493.88,
};

// Configuration constants for note grouping (user-configurable)
const DEFAULT_STABILITY_THRESHOLD_MS = 100;        // For same note repetition
const DEFAULT_TRANSITION_THRESHOLD_MS = 50;        // For note changes
const DEFAULT_SILENCE_THRESHOLD_MS = 200;          // For silence breaks

export class AudioProcessor {
  private audioContext: AudioContext | null = null;
  private analyser: AnalyserNode | null = null;
  private microphone: MediaStreamAudioSourceNode | null = null;
  private dataArray: Uint8Array | null = null;
  private animationFrameId: number | null = null;
  private onNoteDetected: ((note: DetectedNote) => void) | null = null;
  private onWaveformData: ((data: Float32Array) => void) | null = null;
  private onRawFrequency: ((frequency: number, timestamp: number) => void) | null = null;
  private waveformBuffer: Float32Array[] = [];
  private maxWaveformBufferSize: number = 250; // Store ~5 seconds at 50fps
  private timeDomainArray: Uint8Array | null = null;
  private tonicFrequency: number = 440; // Default to A4

  // Note grouping state variables (user-configurable)
  private lastCommittedNote: string | null = null;
  private currentNote: string | null = null;
  private noteStartTime: number | null = null;
  private silenceStartTime: number | null = null;
  
  // User-configurable thresholds
  private stabilityThresholdMs: number = DEFAULT_STABILITY_THRESHOLD_MS;
  private transitionThresholdMs: number = DEFAULT_TRANSITION_THRESHOLD_MS;
  private silenceThresholdMs: number = DEFAULT_SILENCE_THRESHOLD_MS;
  private isGroupingEnabled: boolean = false;

  constructor() {
    this.initializeAudioContext();
  }

  private async initializeAudioContext(): Promise<void> {
    try {
      this.audioContext = new (window.AudioContext || (window as any).webkitAudioContext)();
      
      // Resume the audio context if it's suspended
      if (this.audioContext.state === 'suspended') {
        await this.audioContext.resume();
      }
    } catch (error) {
      console.error('Error initializing audio context:', error);
    }
  }

  public setTonic(tonicNote: string): void {
    this.tonicFrequency = NOTE_FREQUENCIES[tonicNote] || 440;
  }

  public setGroupingEnabled(enabled: boolean): void {
    this.isGroupingEnabled = enabled;
    if (!enabled) {
      this.resetNoteGroupingState();
    }
  }

  public setThresholds(stability: number, transition: number, silence: number): void {
    this.stabilityThresholdMs = stability;
    this.transitionThresholdMs = transition;
    this.silenceThresholdMs = silence;
  }

  public getThresholds(): { stability: number; transition: number; silence: number; groupingEnabled: boolean } {
    return {
      stability: this.stabilityThresholdMs,
      transition: this.transitionThresholdMs,
      silence: this.silenceThresholdMs,
      groupingEnabled: this.isGroupingEnabled
    };
  }

  public async startMicrophoneInput(): Promise<void> {
    if (!this.audioContext) {
      await this.initializeAudioContext();
    }

    // Ensure audio context is running
    if (this.audioContext && this.audioContext.state === 'suspended') {
      await this.audioContext.resume();
    }

    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      
      if (!this.audioContext) {
        throw new Error('Audio context not available');
      }

      this.microphone = this.audioContext.createMediaStreamSource(stream);
      this.analyser = this.audioContext.createAnalyser();
      
      this.analyser.fftSize = 2048;
      this.analyser.smoothingTimeConstant = 0.8;
      
      this.microphone.connect(this.analyser);
      
      const bufferLength = this.analyser.frequencyBinCount;
      this.dataArray = new Uint8Array(bufferLength);
      this.timeDomainArray = new Uint8Array(this.analyser.fftSize);
      
      // Reset note grouping state when starting
      this.resetNoteGroupingState();
      this.waveformBuffer = [];
      
      this.startAnalysis();
    } catch (error) {
      console.error('Error accessing microphone:', error);
      throw error;
    }
  }

  public stopMicrophoneInput(): void {
    if (this.animationFrameId) {
      cancelAnimationFrame(this.animationFrameId);
      this.animationFrameId = null;
    }

    if (this.microphone) {
      this.microphone.disconnect();
      this.microphone = null;
    }

    if (this.analyser) {
      this.analyser.disconnect();
      this.analyser = null;
    }

    this.waveformBuffer = [];
    this.timeDomainArray = null;
  }

  public setOnNoteDetected(callback: (note: DetectedNote) => void): void {
    this.onNoteDetected = callback;
  }

  public setOnWaveformData(callback: (data: Float32Array) => void): void {
    this.onWaveformData = callback;
  }

  public setOnRawFrequency(callback: (frequency: number, timestamp: number) => void): void {
    this.onRawFrequency = callback;
  }

  public getSampleRate(): number {
    return this.audioContext?.sampleRate || 44100;
  }

  private resetNoteGroupingState(): void {
    this.lastCommittedNote = null;
    this.currentNote = null;
    this.noteStartTime = null;
    this.silenceStartTime = null;
  }

  private processAudioFrame(detectedNote: DetectedNote | null): void {
    if (!this.isGroupingEnabled) {
      // Simple real-time detection - display all detected notes immediately
      if (detectedNote && this.onNoteDetected) {
        this.onNoteDetected(detectedNote);
      }
      return;
    }

    // Grouping enabled - use threshold-based logic
    const currentTime = Date.now();

    // CASE 1: A note is detected
    if (detectedNote) {
      // Reset silence timer
      this.silenceStartTime = null;

      const noteName = detectedNote.swara.name;

      // If the detected note is different from current note
      if (noteName !== this.currentNote) {
        // If we have a current note and it's been stable long enough, commit it
        if (this.currentNote && this.noteStartTime) {
          const timeHeld = currentTime - this.noteStartTime;
          if (timeHeld >= this.stabilityThresholdMs) {
            if (this.currentNote !== this.lastCommittedNote) {
              if (this.onNoteDetected) {
                // Create a note object for the current note
                const currentNoteObj = this.createNoteObject(this.currentNote, detectedNote);
                this.onNoteDetected(currentNoteObj);
              }
              this.lastCommittedNote = this.currentNote;
            }
          }
        }

        // Start tracking the new note
        this.currentNote = noteName;
        this.noteStartTime = currentTime;
      }
      // If the same note continues, check if it's been held long enough
      else if (this.currentNote && this.noteStartTime) {
        const timeHeld = currentTime - this.noteStartTime;
        
        // For transitions (different from last committed), use shorter threshold
        const threshold = (this.currentNote !== this.lastCommittedNote) ? 
          this.transitionThresholdMs : this.stabilityThresholdMs;
        
        if (timeHeld >= threshold) {
          if (this.currentNote !== this.lastCommittedNote) {
            if (this.onNoteDetected) {
              this.onNoteDetected(detectedNote);
            }
            this.lastCommittedNote = this.currentNote;
          }
        }
      }
    }
    // CASE 2: No note is detected (silence)
    else {
      // If we have a current note and it's been stable, commit it before silence
      if (this.currentNote && this.noteStartTime) {
        const timeHeld = currentTime - this.noteStartTime;
        if (timeHeld >= this.stabilityThresholdMs) {
          if (this.currentNote !== this.lastCommittedNote) {
            if (this.onNoteDetected) {
              // Create a note object for the current note
              const currentNoteObj = this.createNoteObject(this.currentNote, null);
              this.onNoteDetected(currentNoteObj);
            }
            this.lastCommittedNote = this.currentNote;
          }
        }
      }

      // Reset current note tracking
      this.currentNote = null;
      this.noteStartTime = null;

      // Start the silence timer if it hasn't started
      if (!this.silenceStartTime) {
        this.silenceStartTime = currentTime;
      }

      // Check if the silence is long enough to be a "break"
      const silenceDuration = currentTime - this.silenceStartTime;
      if (silenceDuration >= this.silenceThresholdMs) {
        // The break is official. Reset the last committed note.
        // This allows the same note to be committed again after the pause.
        this.lastCommittedNote = null;
      }
    }
  }

  private createNoteObject(noteName: string, referenceNote: DetectedNote | null): DetectedNote {
    // Find the swara note by name
    const swara = SWARA_NOTES.find(s => s.name === noteName);
    if (!swara) {
      throw new Error(`Unknown note: ${noteName}`);
    }

    // Use reference note data if available, otherwise create default
    const frequency = referenceNote?.frequency || (this.tonicFrequency * Math.pow(2, swara.semitones / 12));
    const confidence = referenceNote?.confidence || 0.8;

    return {
      swara: {
        ...swara,
        frequency: frequency
      },
      timestamp: Date.now(),
      confidence: confidence,
      frequency: frequency
    };
  }

  private startAnalysis(): void {
    if (!this.analyser || !this.dataArray || !this.timeDomainArray) return;

    const analyze = () => {
      this.analyser!.getByteFrequencyData(this.dataArray! as any);
      
      const sampleRate = this.analyser!.context.sampleRate;
      const timestamp = Date.now();
      
      // For live mode, emit raw frequency from frequency domain (faster for real-time)
      // This gives us continuous frequency updates at ~60fps
      const frequency = this.detectFundamentalFrequencyFromArray(this.dataArray!, sampleRate);
      
      // Emit raw frequency for visualization (before swara mapping)
      if (this.onRawFrequency && frequency > 0) {
        this.onRawFrequency(frequency, timestamp);
      }
      
      let detectedNote: DetectedNote | null = null;
      
      if (frequency > 0) {
        detectedNote = this.frequencyToSwara(frequency);
      }
      
      // Use the new note grouping logic
      this.processAudioFrame(detectedNote);

      // Capture time-domain data for waveform visualization
      if (this.onWaveformData && this.timeDomainArray) {
        this.analyser!.getByteTimeDomainData(this.timeDomainArray);
        
        // Convert Uint8Array to Float32Array (normalize from 0-255 to -1 to 1)
        const floatData = new Float32Array(this.timeDomainArray.length);
        for (let i = 0; i < this.timeDomainArray.length; i++) {
          floatData[i] = (this.timeDomainArray[i] - 128) / 128.0;
        }
        
        // Keep a rolling buffer of waveform data
        this.waveformBuffer.push(floatData);
        if (this.waveformBuffer.length > this.maxWaveformBufferSize) {
          this.waveformBuffer.shift();
        }
        
        // Combine all buffers and send
        const totalLength = this.waveformBuffer.reduce((sum, buf) => sum + buf.length, 0);
        const combined = new Float32Array(totalLength);
        let offset = 0;
        for (const buf of this.waveformBuffer) {
          combined.set(buf, offset);
          offset += buf.length;
        }
        this.onWaveformData(combined);
      }

      this.animationFrameId = requestAnimationFrame(analyze);
    };

    analyze();
  }

  private detectFundamentalFrequency(): number {
    if (!this.analyser || !this.dataArray) return 0;

    const sampleRate = this.analyser.context.sampleRate;
    const bufferLength = this.analyser.frequencyBinCount;
    
    // Find the peak frequency
    let maxValue = 0;
    let maxIndex = 0;
    
    for (let i = 0; i < bufferLength; i++) {
      if (this.dataArray[i] > maxValue) {
        maxValue = this.dataArray[i];
        maxIndex = i;
      }
    }
    
    // Convert index to frequency
    const frequency = (maxIndex * sampleRate) / (bufferLength * 2);
    
    // Only return frequencies in a reasonable range (80Hz to 2000Hz)
    return frequency >= 80 && frequency <= 2000 ? frequency : 0;
  }

  private frequencyToSwara(frequency: number): DetectedNote | null {
    if (frequency <= 0) return null;

    // Calculate the closest swara based on the tonic
    const semitones = Math.round(12 * Math.log2(frequency / this.tonicFrequency));
    const normalizedSemitones = ((semitones % 12) + 12) % 12;
    
    const swara = SWARA_NOTES[normalizedSemitones];
    if (!swara) {
      return null;
    }

    // Calculate confidence based on how close the frequency is to the expected frequency
    const expectedFrequency = this.tonicFrequency * Math.pow(2, semitones / 12);
    const frequencyDifference = Math.abs(frequency - expectedFrequency);
    const confidence = Math.max(0, 1 - (frequencyDifference / (expectedFrequency * 0.1)));

    return {
      swara: {
        ...swara,
        frequency: expectedFrequency
      },
      timestamp: Date.now(),
      confidence: Math.min(1, confidence),
      frequency: frequency
    };
  }

  public async processAudioFile(file: File): Promise<DetectedNote[]> {
    return new Promise((resolve, reject) => {
      const audio = new Audio();
      const audioContext = new (window.AudioContext || (window as any).webkitAudioContext)();
      
      audio.addEventListener('loadeddata', async () => {
        try {
          const source = audioContext.createMediaElementSource(audio);
          const analyser = audioContext.createAnalyser();
          
          analyser.fftSize = 2048;
          source.connect(analyser);
          analyser.connect(audioContext.destination);
          
          const bufferLength = analyser.frequencyBinCount;
          const dataArray = new Uint8Array(bufferLength);
          
          const detectedNotes: DetectedNote[] = [];
          const startTime = Date.now();
          
          const analyze = () => {
            analyser.getByteFrequencyData(dataArray);
            
            const frequency = this.detectFundamentalFrequencyFromArray(dataArray, audioContext.sampleRate);
            const timestamp = Date.now() - startTime;
            
            // Emit raw frequency data (before swara mapping) for visualization
            if (this.onRawFrequency && frequency > 0) {
              this.onRawFrequency(frequency, timestamp);
            }
            
            if (frequency > 0) {
              const note = this.frequencyToSwara(frequency);
              if (note) {
                note.timestamp = timestamp;
                detectedNotes.push(note);
              }
            }
            
            if (!audio.paused && !audio.ended) {
              requestAnimationFrame(analyze);
            } else {
              resolve(detectedNotes);
            }
          };
          
          audio.play();
          analyze();
          
        } catch (error) {
          reject(error);
        }
      });
      
      audio.addEventListener('error', () => {
        reject(new Error('Error loading audio file'));
      });
      
      audio.src = URL.createObjectURL(file);
    });
  }

  private detectFundamentalFrequencyFromArray(dataArray: Uint8Array, sampleRate: number): number {
    let maxValue = 0;
    let maxIndex = 0;
    
    // Find the peak frequency
    for (let i = 0; i < dataArray.length; i++) {
      if (dataArray[i] > maxValue) {
        maxValue = dataArray[i];
        maxIndex = i;
      }
    }
    
    const frequency = (maxIndex * sampleRate) / (dataArray.length * 2);
    
    // Check if there's any significant audio
    if (maxValue > 3) { // Very low threshold
      // Try to find a better fundamental frequency using harmonic analysis
      const fundamentalFreq = this.findFundamentalFrequency(dataArray, sampleRate, maxIndex);
      if (fundamentalFreq > 0) {
        return fundamentalFreq >= 80 && fundamentalFreq <= 2000 ? fundamentalFreq : 0;
      }
    }
    
    return frequency >= 80 && frequency <= 2000 ? frequency : 0;
  }

  private findFundamentalFrequency(dataArray: Uint8Array, sampleRate: number, peakIndex: number): number {
    // Look for harmonics to find the fundamental frequency
    const peakFreq = (peakIndex * sampleRate) / (dataArray.length * 2);
    const peakValue = dataArray[peakIndex];
    
    // Check if this might be a harmonic of a lower frequency
    for (let harmonic = 2; harmonic <= 8; harmonic++) {
      const fundamentalFreq = peakFreq / harmonic;
      if (fundamentalFreq >= 80 && fundamentalFreq <= 500) { // Reasonable range for voice/instruments
        const fundamentalIndex = Math.round((fundamentalFreq * dataArray.length * 2) / sampleRate);
        if (fundamentalIndex < dataArray.length && dataArray[fundamentalIndex] > peakValue * 0.3) {
          return fundamentalFreq;
        }
      }
    }
    
    return peakFreq;
  }

  public detectTonicFromFile(file: File): Promise<string> {
    return new Promise((resolve, reject) => {
      const audio = new Audio();
      
      audio.addEventListener('loadeddata', async () => {
        try {
          const audioContext = new (window.AudioContext || (window as any).webkitAudioContext)();
          const source = audioContext.createMediaElementSource(audio);
          const analyser = audioContext.createAnalyser();
          
          analyser.fftSize = 4096;
          source.connect(analyser);
          analyser.connect(audioContext.destination);
          
          const bufferLength = analyser.frequencyBinCount;
          const dataArray = new Uint8Array(bufferLength);
          
          // Play first 3 seconds to detect tonic
          audio.currentTime = 0;
          audio.play();
          
          setTimeout(() => {
            analyser.getByteFrequencyData(dataArray);
            
            // Find the most prominent frequency
            let maxValue = 0;
            let maxIndex = 0;
            
            for (let i = 0; i < bufferLength; i++) {
              if (dataArray[i] > maxValue) {
                maxValue = dataArray[i];
                maxIndex = i;
              }
            }
            
            const frequency = (maxIndex * audioContext.sampleRate) / (bufferLength * 2);
            const tonicNote = this.frequencyToNote(frequency);
            
            audio.pause();
            resolve(tonicNote);
          }, 3000);
          
        } catch (error) {
          reject(error);
        }
      });
      
      audio.addEventListener('error', () => {
        reject(new Error('Error loading audio file'));
      });
      
      audio.src = URL.createObjectURL(file);
    });
  }

  private frequencyToNote(frequency: number): string {
    const A4 = 440;
    const semitones = Math.round(12 * Math.log2(frequency / A4));
    const noteIndex = ((semitones % 12) + 12) % 12;
    
    const notes = ['A', 'A#', 'B', 'C', 'C#', 'D', 'D#', 'E', 'F', 'F#', 'G', 'G#'];
    return notes[noteIndex];
  }

  public async decodeAudioFile(file: File): Promise<{ audioData: Float32Array; sampleRate: number }> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      
      reader.onload = async (e) => {
        try {
          const arrayBuffer = e.target?.result as ArrayBuffer;
          if (!this.audioContext) {
            await this.initializeAudioContext();
          }
          
          if (!this.audioContext) {
            reject(new Error('Audio context not available'));
            return;
          }
          
          const audioBuffer = await this.audioContext.decodeAudioData(arrayBuffer);
          const channelData = audioBuffer.getChannelData(0); // Get first channel
          const sampleRate = audioBuffer.sampleRate;
          
          resolve({
            audioData: channelData,
            sampleRate: sampleRate
          });
        } catch (error) {
          reject(error);
        }
      };
      
      reader.onerror = () => {
        reject(new Error('Error reading audio file'));
      };
      
      reader.readAsArrayBuffer(file);
    });
  }

  // Continuous pitch tracking using autocorrelation on time-domain data
  private detectPitchFromTimeDomain(audioData: Float32Array, sampleRate: number, windowSize: number = 2048, hopSize: number = 512): number[] {
    const pitches: number[] = [];
    const minLag = Math.floor(sampleRate / 2000); // Max frequency ~2000Hz
    const maxLag = Math.floor(sampleRate / 80);  // Min frequency ~80Hz
    
    // Check if we have enough data
    if (audioData.length < windowSize) {
      return [];
    }
    
    for (let i = 0; i <= audioData.length - windowSize; i += hopSize) {
      const window = audioData.slice(i, i + windowSize);
      
      // Check signal energy - skip if too quiet
      let energy = 0;
      for (let j = 0; j < window.length; j++) {
        energy += window[j] * window[j];
      }
      energy = energy / window.length;
      
      if (energy < 0.001) {
        pitches.push(0); // Silence
        continue;
      }
      
      // Apply window function (Hann window)
      const windowed = new Float32Array(window.length);
      for (let j = 0; j < window.length; j++) {
        const hann = 0.5 * (1 - Math.cos(2 * Math.PI * j / (window.length - 1)));
        windowed[j] = window[j] * hann;
      }
      
      // Autocorrelation
      let maxCorrelation = -Infinity;
      let bestLag = 0;
      
      for (let lag = minLag; lag < maxLag && lag < windowed.length / 2; lag++) {
        let correlation = 0;
        const validLength = windowed.length - lag;
        for (let j = 0; j < validLength; j++) {
          correlation += windowed[j] * windowed[j + lag];
        }
        correlation = correlation / validLength; // Normalize
        
        if (correlation > maxCorrelation) {
          maxCorrelation = correlation;
          bestLag = lag;
        }
      }
      
      // Convert lag to frequency
      const frequency = bestLag > 0 ? sampleRate / bestLag : 0;
      
      // Only add valid frequencies
      if (frequency >= 80 && frequency <= 2000 && maxCorrelation > 0.1) {
        pitches.push(frequency);
      } else {
        pitches.push(0); // Silence or invalid
      }
    }
    
    return pitches;
  }

  public async extractContinuousPitchTrack(file: File): Promise<RawFrequencyData[]> {
    const { audioData, sampleRate } = await this.decodeAudioFile(file);
    
    // Use smaller hop size for smoother pitch track (more samples)
    const hopSize = 256; // Process every 256 samples (~5.8ms at 44.1kHz)
    const pitches = this.detectPitchFromTimeDomain(audioData, sampleRate, 2048, hopSize);
    
    const pitchData: RawFrequencyData[] = [];
    const startTime = 0;
    
    for (let i = 0; i < pitches.length; i++) {
      const timeMs = (i * hopSize / sampleRate) * 1000; // Convert to milliseconds
      pitchData.push({
        frequency: pitches[i],
        timestamp: timeMs
      });
    }
    
    return pitchData;
  }
}
