import React, { useRef, useEffect } from 'react';
import {
  Box,
  Paper,
  Typography,
  Chip,
  LinearProgress,
  TextField,
  Button,
  useTheme,
} from '@mui/material';
import { Clear } from '@mui/icons-material';
import { DetectedNote } from '../types';

// Swara color mapping for visual distinction
const SWARA_COLORS: { [key: string]: { gradient: string; text: string } } = {
  S: { gradient: 'linear-gradient(135deg, #FFD700 0%, #FFA500 100%)', text: '#1a1a2e' },
  R: { gradient: 'linear-gradient(135deg, #FF8C5A 0%, #FF6B35 100%)', text: '#ffffff' },
  G: { gradient: 'linear-gradient(135deg, #2ECC71 0%, #27AE60 100%)', text: '#ffffff' },
  M: { gradient: 'linear-gradient(135deg, #3498DB 0%, #2980B9 100%)', text: '#ffffff' },
  P: { gradient: 'linear-gradient(135deg, #9B59B6 0%, #7B2D8E 100%)', text: '#ffffff' },
  D: { gradient: 'linear-gradient(135deg, #E91E63 0%, #C2185B 100%)', text: '#ffffff' },
  N: { gradient: 'linear-gradient(135deg, #00BCD4 0%, #0097A7 100%)', text: '#ffffff' },
};

const getSwaraColor = (symbol: string) => {
  const base = symbol.charAt(0);
  return SWARA_COLORS[base] || { gradient: 'linear-gradient(135deg, #666 0%, #444 100%)', text: '#ffffff' };
};

interface NoteVisualizationProps {
  currentNote: DetectedNote | null;
  detectedNotes: DetectedNote[];
  isLive: boolean;
  onClearNotes?: () => void;
}

const NoteVisualization: React.FC<NoteVisualizationProps> = ({
  currentNote,
  detectedNotes,
  isLive,
  onClearNotes,
}) => {
  const theme = useTheme();
  const notesScrollRef = useRef<HTMLDivElement>(null);
  const isDark = theme.palette.mode === 'dark';

  // Auto-scroll to bottom when new notes are detected
  useEffect(() => {
    if (notesScrollRef.current && detectedNotes.length > 0) {
      notesScrollRef.current.scrollTop = notesScrollRef.current.scrollHeight;
    }
  }, [detectedNotes]);
  const getConfidenceColor = (confidence: number) => {
    if (confidence >= 0.8) return 'success';
    if (confidence >= 0.6) return 'warning';
    return 'error';
  };


  const formatNotesSequence = (notes: DetectedNote[]): string => {
    if (notes.length === 0) return '';
    
    let result = '';
    let lastNoteName = '';
    let lastNoteTimestamp = 0;
    
    for (let i = 0; i < notes.length; i++) {
      const note = notes[i];
      const noteName = note.swara.symbol;
      
      // Check if this is a break (different note or first note)
      if (i === 0 || noteName !== lastNoteName) {
        // Add dots for break if not the first note and break is long enough
        if (i > 0) {
          const breakDuration = note.timestamp - lastNoteTimestamp;
          const dots = getDotsForDuration(breakDuration);
          if (dots) { // Only add dots if there are any (no dots for quick transitions)
            result += `${dots} `;
          }
        }
        
        lastNoteName = noteName;
        lastNoteTimestamp = note.timestamp;
        
        // Add the note with 2nd character in subscript (if it exists)
        result += formatNoteWithSubscript(noteName);
      } else {
        // Same note, just add it again without counting
        lastNoteTimestamp = note.timestamp;
        result += formatNoteWithSubscript(noteName);
      }
    }
    
    return result;
  };

  const getDotsForDuration = (durationMs: number): string => {
    // Convert duration to dots based on time intervals
    // 0-500ms: no dots (quick transitions)
    // 500-1000ms: 2 dots (..)
    // 1000-2000ms: 3 dots (...)
    // 2000-3000ms: 4 dots (....)
    // 3000ms+: 5 dots (.....)
    
    if (durationMs < 500) return ''; // No dots for quick transitions
    if (durationMs < 1000) return '..';
    if (durationMs < 2000) return '...';
    if (durationMs < 3000) return '....';
    return '.....';
  };

  const formatNoteWithSubscript = (noteSymbol: string): string => {
    // If the note symbol has a 2nd character (like R1, R2, G2, etc.), show it in subscript
    if (noteSymbol.length > 1) {
      const firstChar = noteSymbol[0];
      const secondChar = noteSymbol[1];
      return `${firstChar}${getSmallSubscript(secondChar)}`;
    }
    // If no 2nd character (like S, P), just return the symbol
    return noteSymbol;
  };

  const getSmallSubscript = (char: string): string => {
    // Use smaller subscript characters for better space efficiency
    const smallSubscripts: { [key: string]: string } = {
      '1': '', '2': '', '3': '', '4': '', '5': '',
      '6': '', '7': '', '8': '', '9': '', '0': ''
    };
    return smallSubscripts[char] || char;
  };

  const swaraColor = currentNote ? getSwaraColor(currentNote.swara.symbol) : null;

  return (
    <Paper 
      elevation={0} 
      sx={{ 
        p: 3,
        background: isDark 
          ? 'linear-gradient(145deg, rgba(27,38,59,0.9) 0%, rgba(13,27,42,0.95) 100%)'
          : 'linear-gradient(145deg, rgba(255,255,255,0.95) 0%, rgba(255,248,240,0.9) 100%)',
        backdropFilter: 'blur(10px)',
        border: '1px solid',
        borderColor: isDark ? 'rgba(255,107,53,0.2)' : 'rgba(123,45,142,0.15)',
        borderRadius: 3,
      }}
    >
      <Typography 
        variant="h6" 
        gutterBottom
        sx={{
          background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
          backgroundClip: 'text',
          WebkitBackgroundClip: 'text',
          WebkitTextFillColor: 'transparent',
          fontWeight: 600,
        }}
      >
        {isLive ? 'Live Note Detection' : 'Detected Notes'}
      </Typography>

      {currentNote && swaraColor && (
        <Box 
          mb={3} 
          p={3} 
          sx={{ 
            background: swaraColor.gradient,
            borderRadius: 3,
            textAlign: 'center',
            position: 'relative',
            overflow: 'hidden',
            animation: 'notePop 0.3s ease-out, glowPulse 2s ease-in-out infinite',
            boxShadow: `0 8px 32px rgba(0,0,0,0.15), 0 0 40px ${currentNote.confidence >= 0.8 ? 'rgba(46,204,113,0.3)' : 'rgba(255,179,71,0.3)'}`,
            '&::before': {
              content: '""',
              position: 'absolute',
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              background: 'radial-gradient(circle at 30% 30%, rgba(255,255,255,0.2) 0%, transparent 50%)',
              pointerEvents: 'none',
            },
          }}
        >
          <Typography 
            variant="h2" 
            component="div" 
            gutterBottom
            sx={{ 
              fontWeight: 'bold',
              color: swaraColor.text,
              textShadow: '2px 2px 8px rgba(0,0,0,0.3)',
              fontFamily: '"Playfair Display", serif',
              position: 'relative',
              zIndex: 1,
            }}
          >
            {currentNote.swara.symbol}
          </Typography>
          
          <Typography 
            variant="h5" 
            gutterBottom
            sx={{ 
              color: swaraColor.text,
              fontWeight: 500,
              opacity: 0.95,
              position: 'relative',
              zIndex: 1,
            }}
          >
            {currentNote.swara.name}
          </Typography>
          
          <Box display="flex" justifyContent="center" gap={2} mb={2} flexWrap="wrap" sx={{ position: 'relative', zIndex: 1 }}>
            <Chip
              label={`${currentNote.frequency.toFixed(1)} Hz`}
              variant="filled"
              size="medium"
              sx={{ 
                backgroundColor: 'rgba(255,255,255,0.25)',
                color: swaraColor.text,
                fontWeight: 'bold',
                backdropFilter: 'blur(4px)',
              }}
            />
            <Chip
              label={`${(currentNote.confidence * 100).toFixed(0)}% confidence`}
              color={getConfidenceColor(currentNote.confidence)}
              size="medium"
              sx={{ fontWeight: 'bold' }}
            />
          </Box>
          
          <LinearProgress
            variant="determinate"
            value={currentNote.confidence * 100}
            color={getConfidenceColor(currentNote.confidence)}
            sx={{ 
              height: 12, 
              borderRadius: 6,
              backgroundColor: 'rgba(255,255,255,0.3)',
              position: 'relative',
              zIndex: 1,
              '& .MuiLinearProgress-bar': {
                borderRadius: 6,
                background: currentNote.confidence >= 0.8 
                  ? 'linear-gradient(90deg, #2ECC71 0%, #27AE60 100%)'
                  : 'linear-gradient(90deg, #FFB347 0%, #FF9800 100%)',
              }
            }}
          />
        </Box>
      )}

      {detectedNotes.length > 0 && (
        <Box>
          <Box display="flex" justifyContent="space-between" alignItems="center" mb={1}>
            <Typography 
              variant="subtitle1"
              sx={{ fontWeight: 600, color: 'text.primary' }}
            >
              Detected Sequence ({detectedNotes.length} notes)
            </Typography>
            {onClearNotes && (
              <Button
                variant="outlined"
                size="small"
                startIcon={<Clear />}
                onClick={onClearNotes}
                sx={{
                  minWidth: 'auto',
                  px: 1.5,
                  py: 0.5,
                  borderColor: theme.palette.primary.main,
                  color: theme.palette.primary.main,
                  '&:hover': {
                    borderColor: theme.palette.primary.dark,
                    backgroundColor: `${theme.palette.primary.main}10`,
                  },
                }}
              >
                Clear
              </Button>
            )}
          </Box>
          
          {/* Scrollable text field with notes and breaks */}
          <Box
            ref={notesScrollRef}
            sx={{
              maxHeight: 200,
              overflowY: 'auto',
              border: '1px solid',
              borderColor: isDark ? 'rgba(255,107,53,0.3)' : 'rgba(123,45,142,0.2)',
              borderRadius: 2,
              backgroundColor: isDark ? 'rgba(13,27,42,0.6)' : 'rgba(255,248,240,0.8)',
            }}
          >
            <TextField
              multiline
              fullWidth
              variant="outlined"
              value={formatNotesSequence(detectedNotes)}
              InputProps={{
                readOnly: true,
                sx: {
                  fontSize: '1.2rem',
                  fontFamily: '"Outfit", monospace',
                  fontWeight: 500,
                  letterSpacing: '1px',
                  color: 'text.primary',
                  '& .MuiInputBase-input': {
                    padding: 2,
                    lineHeight: 1.8,
                    border: 'none',
                    '&:focus': {
                      outline: 'none',
                    },
                  },
                  '& .MuiOutlinedInput-notchedOutline': {
                    border: 'none',
                  },
                },
              }}
              sx={{
                '& .MuiOutlinedInput-root': {
                  backgroundColor: 'transparent',
                  minHeight: 120,
                },
              }}
            />
          </Box>
          
          {/* Summary */}
          <Box 
            mt={2} 
            p={2} 
            sx={{ 
              background: isDark 
                ? 'linear-gradient(135deg, rgba(255,107,53,0.1) 0%, rgba(123,45,142,0.1) 100%)'
                : 'linear-gradient(135deg, rgba(255,107,53,0.08) 0%, rgba(123,45,142,0.08) 100%)',
              borderRadius: 2,
              border: '1px solid',
              borderColor: isDark ? 'rgba(255,107,53,0.2)' : 'rgba(123,45,142,0.15)',
            }}
          >
            <Typography variant="body2" color="text.secondary">
              <strong style={{ color: theme.palette.primary.main }}>Latest:</strong> {detectedNotes[detectedNotes.length - 1]?.swara.name || 'None'} 
              {'  '}
              <strong style={{ color: theme.palette.secondary.main }}>Total:</strong> {detectedNotes.length} notes detected
              {isLive && (
                <>
                  {'  '}
                  <Box 
                    component="span" 
                    sx={{ 
                      color: currentNote ? 'success.main' : 'text.secondary',
                      fontWeight: 600,
                    }}
                  >
                    {currentNote ? ' Detecting' : ' Listening...'}
                  </Box>
                </>
              )}
            </Typography>
          </Box>
        </Box>
      )}

      {!currentNote && detectedNotes.length === 0 && (
        <Box 
          textAlign="center" 
          py={5}
          sx={{
            background: isDark 
              ? 'radial-gradient(circle at center, rgba(255,107,53,0.05) 0%, transparent 70%)'
              : 'radial-gradient(circle at center, rgba(123,45,142,0.05) 0%, transparent 70%)',
            borderRadius: 2,
          }}
        >
          <Box
            sx={{
              width: 60,
              height: 60,
              borderRadius: '50%',
              background: `linear-gradient(135deg, ${theme.palette.primary.main}30 0%, ${theme.palette.secondary.main}30 100%)`,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              margin: '0 auto',
              mb: 2,
              animation: 'float 3s ease-in-out infinite',
            }}
          >
            <Typography variant="h5" sx={{ opacity: 0.6 }}></Typography>
          </Box>
          <Typography variant="body1" color="text.secondary" sx={{ fontWeight: 500 }}>
            {isLive
              ? 'Start recording to see live note detection'
              : 'Upload an audio file to see detected notes'}
          </Typography>
        </Box>
      )}
    </Paper>
  );
};

export default NoteVisualization;
