import React, { useCallback } from 'react';
import { useDropzone } from 'react-dropzone';
import {
  Box,
  Paper,
  Typography,
  LinearProgress,
  Alert,
  IconButton,
  useTheme,
} from '@mui/material';
import {
  CloudUpload,
  AudioFile,
  Delete,
  PlayArrow,
  Pause,
} from '@mui/icons-material';

interface FileUploadProps {
  file: File | null;
  isUploading: boolean;
  isProcessing: boolean;
  error: string | null;
  onFileSelect: (file: File) => void;
  onFileRemove: () => void;
  onPlay: () => void;
  onPause: () => void;
  isPlaying: boolean;
}

const FileUpload: React.FC<FileUploadProps> = ({
  file,
  isUploading,
  isProcessing,
  error,
  onFileSelect,
  onFileRemove,
  onPlay,
  onPause,
  isPlaying,
}) => {
  const theme = useTheme();
  const isDark = theme.palette.mode === 'dark';

  const onDrop = useCallback((acceptedFiles: File[]) => {
    if (acceptedFiles.length > 0) {
      onFileSelect(acceptedFiles[0]);
    }
  }, [onFileSelect]);

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      'audio/*': ['.mp3', '.wav', '.m4a', '.ogg', '.aac'],
    },
    multiple: false,
    disabled: isUploading || isProcessing,
  });

  if (file) {
    return (
      <Paper
        elevation={0}
        sx={{
          p: 3,
          textAlign: 'center',
          background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
          color: 'white',
          borderRadius: 3,
          position: 'relative',
          overflow: 'hidden',
          '&::before': {
            content: '""',
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            background: 'radial-gradient(circle at 30% 30%, rgba(255,255,255,0.15) 0%, transparent 50%)',
            pointerEvents: 'none',
          },
        }}
      >
        <Box display="flex" alignItems="center" justifyContent="space-between" position="relative" zIndex={1}>
          <Box display="flex" alignItems="center" gap={2}>
            <Box
              sx={{
                width: 48,
                height: 48,
                borderRadius: '50%',
                backgroundColor: 'rgba(255,255,255,0.2)',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                backdropFilter: 'blur(4px)',
              }}
            >
              <AudioFile sx={{ fontSize: 28 }} />
            </Box>
            <Box textAlign="left">
              <Typography variant="h6" component="div" sx={{ fontWeight: 600 }}>
                {file.name}
              </Typography>
              <Typography variant="body2" sx={{ opacity: 0.9 }}>
                {(file.size / (1024 * 1024)).toFixed(2)} MB
              </Typography>
            </Box>
          </Box>
          
          <Box display="flex" gap={1}>
            <IconButton
              onClick={isPlaying ? onPause : onPlay}
              disabled={isProcessing}
              sx={{ 
                color: 'white',
                backgroundColor: 'rgba(255,255,255,0.2)',
                backdropFilter: 'blur(4px)',
                transition: 'all 0.3s ease',
                '&:hover': {
                  backgroundColor: 'rgba(255,255,255,0.3)',
                  transform: 'scale(1.05)',
                },
              }}
            >
              {isPlaying ? <Pause /> : <PlayArrow />}
            </IconButton>
            <IconButton
              onClick={onFileRemove}
              disabled={isProcessing}
              sx={{ 
                color: 'white',
                backgroundColor: 'rgba(255,255,255,0.2)',
                backdropFilter: 'blur(4px)',
                transition: 'all 0.3s ease',
                '&:hover': {
                  backgroundColor: 'rgba(255,0,0,0.3)',
                  transform: 'scale(1.05)',
                },
              }}
            >
              <Delete />
            </IconButton>
          </Box>
        </Box>

        {isProcessing && (
          <Box mt={3} position="relative" zIndex={1}>
            <Typography variant="body2" gutterBottom sx={{ fontWeight: 500 }}>
              Processing audio...
            </Typography>
            <LinearProgress 
              sx={{
                height: 8,
                borderRadius: 4,
                backgroundColor: 'rgba(255,255,255,0.3)',
                '& .MuiLinearProgress-bar': {
                  background: 'linear-gradient(90deg, rgba(255,255,255,0.9) 0%, rgba(255,255,255,0.6) 100%)',
                  borderRadius: 4,
                },
              }}
            />
          </Box>
        )}
      </Paper>
    );
  }

  return (
    <Paper
      {...getRootProps()}
      elevation={0}
      sx={{
        p: 4,
        textAlign: 'center',
        cursor: 'pointer',
        border: '2px dashed',
        borderColor: isDragActive 
          ? theme.palette.primary.main 
          : isDark ? 'rgba(255,107,53,0.3)' : 'rgba(123,45,142,0.2)',
        backgroundColor: isDragActive 
          ? `${theme.palette.primary.main}10`
          : isDark ? 'rgba(27,38,59,0.5)' : 'rgba(255,248,240,0.5)',
        borderRadius: 3,
        transition: 'all 0.3s ease-in-out',
        '&:hover': {
          borderColor: theme.palette.primary.main,
          backgroundColor: `${theme.palette.primary.main}10`,
          transform: 'translateY(-2px)',
          boxShadow: `0 8px 25px ${theme.palette.primary.main}20`,
        },
      }}
    >
      <input {...getInputProps()} />
      
      <Box
        sx={{
          width: 80,
          height: 80,
          borderRadius: '50%',
          background: isDragActive
            ? `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`
            : `linear-gradient(135deg, ${theme.palette.primary.main}30 0%, ${theme.palette.secondary.main}30 100%)`,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          margin: '0 auto',
          mb: 2,
          transition: 'all 0.3s ease',
          animation: isDragActive ? 'pulse 1s ease-in-out infinite' : 'float 3s ease-in-out infinite',
          '@keyframes float': {
            '0%, 100%': { transform: 'translateY(0px)' },
            '50%': { transform: 'translateY(-5px)' },
          },
        }}
      >
        <CloudUpload
          sx={{
            fontSize: 40,
            color: isDragActive ? 'white' : theme.palette.primary.main,
            transition: 'all 0.3s ease',
          }}
        />
      </Box>
      
      <Typography 
        variant="h6" 
        gutterBottom
        sx={{
          fontWeight: 600,
          color: isDragActive ? theme.palette.primary.main : 'text.primary',
        }}
      >
        {isDragActive
          ? 'Drop your audio file here'
          : 'Drag & drop an audio file here'}
      </Typography>
      
      <Typography variant="body2" color="text.secondary" gutterBottom sx={{ fontWeight: 500 }}>
        or click to browse
      </Typography>
      
      <Box 
        sx={{ 
          display: 'flex', 
          gap: 1, 
          justifyContent: 'center', 
          flexWrap: 'wrap',
          mt: 2,
        }}
      >
        {['MP3', 'WAV', 'M4A', 'OGG', 'AAC'].map((format) => (
          <Box
            key={format}
            sx={{
              px: 1.5,
              py: 0.5,
              borderRadius: 1,
              backgroundColor: isDark ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.05)',
              fontSize: '0.75rem',
              fontWeight: 500,
              color: 'text.secondary',
            }}
          >
            {format}
          </Box>
        ))}
      </Box>

      {isUploading && (
        <Box mt={3}>
          <LinearProgress 
            sx={{
              height: 6,
              borderRadius: 3,
              backgroundColor: isDark ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.05)',
              '& .MuiLinearProgress-bar': {
                background: `linear-gradient(90deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
                borderRadius: 3,
              },
            }}
          />
          <Typography variant="body2" sx={{ mt: 1, fontWeight: 500 }}>
            Uploading...
          </Typography>
        </Box>
      )}

      {error && (
        <Alert 
          severity="error" 
          sx={{ 
            mt: 2,
            borderRadius: 2,
          }}
        >
          {error}
        </Alert>
      )}
    </Paper>
  );
};

export default FileUpload;
