import React from 'react';
import {
  Box,
  Paper,
  Typography,
  Button,
  Alert,
  CircularProgress,
  useTheme,
} from '@mui/material';
import {
  Mic,
  Stop,
  PlayArrow,
} from '@mui/icons-material';

interface MicrophoneInputProps {
  isRecording: boolean;
  isProcessing: boolean;
  hasPermission: boolean;
  error: string | null;
  onStartRecording: () => void;
  onStopRecording: () => void;
  onRequestPermission: () => void;
}

const MicrophoneInput: React.FC<MicrophoneInputProps> = ({
  isRecording,
  isProcessing,
  hasPermission,
  error,
  onStartRecording,
  onStopRecording,
  onRequestPermission,
}) => {
  const theme = useTheme();
  const isDark = theme.palette.mode === 'dark';

  if (!hasPermission) {
    return (
      <Paper 
        elevation={0} 
        sx={{ 
          p: 3, 
          textAlign: 'center',
          background: isDark 
            ? 'linear-gradient(145deg, rgba(27,38,59,0.9) 0%, rgba(13,27,42,0.95) 100%)'
            : 'linear-gradient(145deg, rgba(255,255,255,0.95) 0%, rgba(255,248,240,0.9) 100%)',
          border: '1px solid',
          borderColor: isDark ? 'rgba(255,107,53,0.2)' : 'rgba(123,45,142,0.15)',
          borderRadius: 3,
        }}
      >
        <Box 
          sx={{
            width: 70,
            height: 70,
            borderRadius: '50%',
            background: isDark ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.05)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            margin: '0 auto',
            mb: 2,
          }}
        >
          <Mic sx={{ fontSize: 36, color: 'text.secondary', opacity: 0.5 }} />
        </Box>
        
        <Typography 
          variant="h6"
          sx={{
            fontWeight: 600,
            mb: 1,
          }}
        >
          Enable Microphone Access
        </Typography>
        
        <Typography variant="body2" color="text.secondary" paragraph sx={{ opacity: 0.8 }}>
          Allow microphone access to start live audio analysis.
        </Typography>

        <Button
          variant="contained"
          size="large"
          startIcon={<Mic />}
          onClick={onRequestPermission}
          sx={{
            px: 4,
            py: 1.5,
            background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
            boxShadow: `0 4px 15px ${theme.palette.primary.main}40`,
            fontWeight: 600,
            borderRadius: 2,
            transition: 'all 0.3s ease',
            '&:hover': {
              background: `linear-gradient(135deg, ${theme.palette.primary.light} 0%, ${theme.palette.secondary.light} 100%)`,
              boxShadow: `0 6px 20px ${theme.palette.primary.main}60`,
              transform: 'translateY(-2px)',
            },
          }}
        >
          Allow Access
        </Button>

        {error && (
          <Alert severity="error" sx={{ mt: 2, borderRadius: 2 }}>
            {error}
          </Alert>
        )}
      </Paper>
    );
  }

  return (
    <Paper
      elevation={0}
      sx={{
        p: 3,
        textAlign: 'center',
        background: isRecording 
          ? `linear-gradient(135deg, ${theme.palette.error.main} 0%, ${theme.palette.error.dark} 100%)`
          : `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
        color: 'white',
        borderRadius: 3,
        transition: 'all 0.3s ease-in-out',
        position: 'relative',
        overflow: 'hidden',
        '&::before': {
          content: '""',
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'radial-gradient(circle at 30% 30%, rgba(255,255,255,0.15) 0%, transparent 50%)',
          pointerEvents: 'none',
        },
      }}
    >
      <Box display="flex" flexDirection="column" alignItems="center" gap={2} position="relative" zIndex={1}>
        {isRecording ? (
          <>
            <Box position="relative">
              {/* Pulsing rings */}
              <Box
                sx={{
                  position: 'absolute',
                  top: '50%',
                  left: '50%',
                  width: 60,
                  height: 60,
                  borderRadius: '50%',
                  border: '2px solid rgba(255,255,255,0.5)',
                  transform: 'translate(-50%, -50%)',
                  animation: 'ringPulse 1.5s ease-out infinite',
                  '@keyframes ringPulse': {
                    '0%': {
                      transform: 'translate(-50%, -50%) scale(1)',
                      opacity: 0.8,
                    },
                    '100%': {
                      transform: 'translate(-50%, -50%) scale(2)',
                      opacity: 0,
                    },
                  },
                }}
              />
              <Box
                sx={{
                  position: 'absolute',
                  top: '50%',
                  left: '50%',
                  width: 60,
                  height: 60,
                  borderRadius: '50%',
                  border: '2px solid rgba(255,255,255,0.5)',
                  transform: 'translate(-50%, -50%)',
                  animation: 'ringPulse 1.5s ease-out infinite 0.5s',
                }}
              />
              
              <Box
                sx={{
                  width: 60,
                  height: 60,
                  borderRadius: '50%',
                  backgroundColor: 'rgba(255,255,255,0.2)',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  backdropFilter: 'blur(4px)',
                }}
              >
                <Mic
                  sx={{
                    fontSize: 32,
                    animation: 'micPulse 1s ease-in-out infinite',
                    '@keyframes micPulse': {
                      '0%, 100%': { transform: 'scale(1)' },
                      '50%': { transform: 'scale(1.1)' },
                    },
                  }}
                />
              </Box>
            </Box>
            
            <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
              Recording... Sing or play
            </Typography>
            
            <Button
              variant="contained"
              size="large"
              startIcon={<Stop />}
              onClick={onStopRecording}
              sx={{
                backgroundColor: 'rgba(255,255,255,0.25)',
                color: 'white',
                fontWeight: 600,
                borderRadius: 2,
                backdropFilter: 'blur(4px)',
                px: 4,
                transition: 'all 0.3s ease',
                '&:hover': {
                  backgroundColor: 'rgba(255,255,255,0.35)',
                  transform: 'scale(1.02)',
                },
              }}
            >
              Stop Recording
            </Button>
          </>
        ) : (
          <>
            <Box
              sx={{
                width: 60,
                height: 60,
                borderRadius: '50%',
                backgroundColor: 'rgba(255,255,255,0.2)',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                backdropFilter: 'blur(4px)',
              }}
            >
              <Mic sx={{ fontSize: 32 }} />
            </Box>
            
            <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
              Ready to analyze live audio
            </Typography>
            
            <Button
              variant="contained"
              size="large"
              startIcon={<PlayArrow />}
              onClick={onStartRecording}
              disabled={isProcessing}
              sx={{
                backgroundColor: 'rgba(255,255,255,0.25)',
                color: 'white',
                fontWeight: 600,
                borderRadius: 2,
                backdropFilter: 'blur(4px)',
                px: 4,
                transition: 'all 0.3s ease',
                '&:hover': {
                  backgroundColor: 'rgba(255,255,255,0.35)',
                  transform: 'scale(1.02)',
                },
                '&.Mui-disabled': {
                  backgroundColor: 'rgba(255,255,255,0.1)',
                  color: 'rgba(255,255,255,0.5)',
                },
              }}
            >
              Start Recording
            </Button>
          </>
        )}

        {isProcessing && (
          <Box display="flex" alignItems="center" gap={1}>
            <CircularProgress size={20} sx={{ color: 'white' }} />
            <Typography variant="body2" sx={{ fontWeight: 500 }}>
              Processing...
            </Typography>
          </Box>
        )}

        {error && (
          <Alert 
            severity="error" 
            sx={{ 
              mt: 1, 
              width: '100%',
              backgroundColor: 'rgba(255,255,255,0.15)',
              color: 'white',
              '& .MuiAlert-icon': {
                color: 'white',
              },
              borderRadius: 2,
            }}
          >
            {error}
          </Alert>
        )}
      </Box>
    </Paper>
  );
};

export default MicrophoneInput;
