import React, { createContext, useContext, useReducer, ReactNode } from 'react';
import { AppState, InputMode, AudioInputState, FileUploadState, AudioAnalysisResult } from '../types';

interface AppContextType {
  state: AppState;
  dispatch: React.Dispatch<AppAction>;
}

type AppAction =
  | { type: 'SET_INPUT_MODE'; payload: InputMode }
  | { type: 'SET_AUDIO_INPUT_STATE'; payload: Partial<AudioInputState> }
  | { type: 'SET_FILE_UPLOAD_STATE'; payload: Partial<FileUploadState> }
  | { type: 'SET_ANALYSIS'; payload: AudioAnalysisResult | null }
  | { type: 'SET_SELECTED_TONIC'; payload: string }
  | { type: 'RESET_STATE' };

const initialState: AppState = {
  inputMode: 'microphone',
  audioInput: {
    isRecording: false,
    isProcessing: false,
    hasPermission: false,
    error: null,
  },
  fileUpload: {
    file: null,
    isUploading: false,
    isProcessing: false,
    error: null,
  },
  analysis: null,
  selectedTonic: 'D',
};

function appReducer(state: AppState, action: AppAction): AppState {
  switch (action.type) {
    case 'SET_INPUT_MODE':
      return {
        ...state,
        inputMode: action.payload,
      };
    case 'SET_AUDIO_INPUT_STATE':
      return {
        ...state,
        audioInput: {
          ...state.audioInput,
          ...action.payload,
        },
      };
    case 'SET_FILE_UPLOAD_STATE':
      return {
        ...state,
        fileUpload: {
          ...state.fileUpload,
          ...action.payload,
        },
      };
    case 'SET_ANALYSIS':
      return {
        ...state,
        analysis: action.payload,
      };
    case 'SET_SELECTED_TONIC':
      return {
        ...state,
        selectedTonic: action.payload,
      };
    case 'RESET_STATE':
      return initialState;
    default:
      return state;
  }
}

const AppContext = createContext<AppContextType | undefined>(undefined);

export const useAppContext = () => {
  const context = useContext(AppContext);
  if (!context) {
    throw new Error('useAppContext must be used within an AppProvider');
  }
  return context;
};

interface AppProviderProps {
  children: ReactNode;
}

export const AppProvider: React.FC<AppProviderProps> = ({ children }) => {
  const [state, dispatch] = useReducer(appReducer, initialState);

  return (
    <AppContext.Provider value={{ state, dispatch }}>
      {children}
    </AppContext.Provider>
  );
};
