import React from 'react';
import {
  Box,
  Paper,
  Typography,
  Slider,
  IconButton,
  Chip,
  useTheme,
} from '@mui/material';
import {
  PlayArrow,
  Pause,
  Stop,
} from '@mui/icons-material';
import { DetectedNote } from '../types';

interface TimelineVisualizationProps {
  detectedNotes: DetectedNote[];
  currentTime: number;
  duration: number;
  isPlaying: boolean;
  onTimeChange: (time: number) => void;
  onPlay: () => void;
  onPause: () => void;
  onStop: () => void;
}

// Swara color mapping
const SWARA_COLORS: { [key: string]: string } = {
  S: '#FFD700',
  R: '#FF6B35',
  G: '#2ECC71',
  M: '#3498DB',
  P: '#9B59B6',
  D: '#E91E63',
  N: '#00BCD4',
};

const getSwaraColor = (symbol: string) => {
  const base = symbol.charAt(0);
  return SWARA_COLORS[base] || '#666';
};

const TimelineVisualization: React.FC<TimelineVisualizationProps> = ({
  detectedNotes,
  currentTime,
  duration,
  isPlaying,
  onTimeChange,
  onPlay,
  onPause,
  onStop,
}) => {
  const theme = useTheme();
  const isDark = theme.palette.mode === 'dark';

  const formatTime = (time: number) => {
    const minutes = Math.floor(time / 60);
    const seconds = Math.floor(time % 60);
    return `${minutes}:${seconds.toString().padStart(2, '0')}`;
  };

  const getCurrentNote = (): DetectedNote | null => {
    if (detectedNotes.length === 0) return null;
    
    // Find the note closest to current time
    const currentNote = detectedNotes.reduce((closest, note) => {
      const closestDiff = Math.abs(closest.timestamp - currentTime);
      const noteDiff = Math.abs(note.timestamp - currentTime);
      return noteDiff < closestDiff ? note : closest;
    });
    
    return currentNote;
  };

  const currentNote = getCurrentNote();
  const currentNoteColor = currentNote ? getSwaraColor(currentNote.swara.symbol) : theme.palette.primary.main;

  return (
    <Paper 
      elevation={0} 
      sx={{ 
        p: 3,
        background: isDark 
          ? 'linear-gradient(145deg, rgba(27,38,59,0.9) 0%, rgba(13,27,42,0.95) 100%)'
          : 'linear-gradient(145deg, rgba(255,255,255,0.95) 0%, rgba(255,248,240,0.9) 100%)',
        backdropFilter: 'blur(10px)',
        border: '1px solid',
        borderColor: isDark ? 'rgba(255,107,53,0.2)' : 'rgba(123,45,142,0.15)',
        borderRadius: 3,
      }}
    >
      <Typography 
        variant="h6" 
        gutterBottom
        sx={{
          background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
          backgroundClip: 'text',
          WebkitBackgroundClip: 'text',
          WebkitTextFillColor: 'transparent',
          fontWeight: 600,
        }}
      >
        Timeline Analysis
      </Typography>

      {detectedNotes.length > 0 ? (
        <>
          {/* Current Note Display */}
          {currentNote && (
            <Box 
              mb={3} 
              textAlign="center"
              sx={{
                p: 2,
                borderRadius: 2,
                background: `linear-gradient(135deg, ${currentNoteColor}20 0%, ${currentNoteColor}10 100%)`,
                border: `2px solid ${currentNoteColor}40`,
                transition: 'all 0.3s ease',
              }}
            >
              <Typography 
                variant="h3" 
                component="div" 
                gutterBottom
                sx={{
                  fontFamily: '"Playfair Display", serif',
                  fontWeight: 700,
                  color: currentNoteColor,
                  textShadow: isDark ? `0 0 20px ${currentNoteColor}50` : 'none',
                }}
              >
                {currentNote.swara.symbol}
              </Typography>
              <Typography 
                variant="h6" 
                color="text.secondary" 
                gutterBottom
                sx={{ fontWeight: 500 }}
              >
                {currentNote.swara.name}
              </Typography>
              <Box display="flex" justifyContent="center" gap={1}>
                <Chip
                  label={`${currentNote.frequency.toFixed(1)} Hz`}
                  size="small"
                  sx={{
                    backgroundColor: isDark ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.05)',
                    fontWeight: 500,
                  }}
                />
                <Chip
                  label={`${(currentNote.confidence * 100).toFixed(0)}%`}
                  size="small"
                  sx={{
                    background: currentNote.confidence >= 0.8 
                      ? 'linear-gradient(135deg, #2ECC71 0%, #27AE60 100%)'
                      : 'linear-gradient(135deg, #FFB347 0%, #FF9800 100%)',
                    color: 'white',
                    fontWeight: 600,
                  }}
                />
              </Box>
            </Box>
          )}

          {/* Timeline Slider */}
          <Box mb={2}>
            <Slider
              value={currentTime}
              min={0}
              max={duration}
              step={0.1}
              onChange={(_, value) => onTimeChange(value as number)}
              valueLabelDisplay="auto"
              valueLabelFormat={formatTime}
              sx={{ 
                mb: 1,
                color: theme.palette.primary.main,
                height: 8,
                '& .MuiSlider-track': {
                  background: `linear-gradient(90deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
                  border: 'none',
                },
                '& .MuiSlider-thumb': {
                  width: 20,
                  height: 20,
                  background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
                  boxShadow: `0 0 10px ${theme.palette.primary.main}50`,
                  '&:hover, &.Mui-focusVisible': {
                    boxShadow: `0 0 20px ${theme.palette.primary.main}70`,
                  },
                },
                '& .MuiSlider-rail': {
                  backgroundColor: isDark ? 'rgba(255,255,255,0.15)' : 'rgba(0,0,0,0.1)',
                },
                '& .MuiSlider-valueLabel': {
                  background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
                  borderRadius: 1,
                },
              }}
            />
            <Box display="flex" justifyContent="space-between">
              <Typography variant="caption" color="text.secondary" sx={{ fontWeight: 500 }}>
                {formatTime(currentTime)}
              </Typography>
              <Typography variant="caption" color="text.secondary" sx={{ fontWeight: 500 }}>
                {formatTime(duration)}
              </Typography>
            </Box>
          </Box>

          {/* Playback Controls */}
          <Box display="flex" justifyContent="center" gap={1} mb={3}>
            <IconButton
              onClick={isPlaying ? onPause : onPlay}
              sx={{
                background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
                color: 'white',
                width: 56,
                height: 56,
                boxShadow: `0 4px 15px ${theme.palette.primary.main}40`,
                transition: 'all 0.3s ease',
                '&:hover': {
                  background: `linear-gradient(135deg, ${theme.palette.primary.light} 0%, ${theme.palette.secondary.light} 100%)`,
                  transform: 'scale(1.05)',
                  boxShadow: `0 6px 20px ${theme.palette.primary.main}60`,
                },
              }}
            >
              {isPlaying ? <Pause /> : <PlayArrow />}
            </IconButton>
            <IconButton 
              onClick={onStop}
              sx={{
                backgroundColor: isDark ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.05)',
                color: 'text.secondary',
                transition: 'all 0.3s ease',
                '&:hover': {
                  backgroundColor: isDark ? 'rgba(255,255,255,0.2)' : 'rgba(0,0,0,0.1)',
                },
              }}
            >
              <Stop />
            </IconButton>
          </Box>

          {/* Notes Overview */}
          <Box>
            <Typography variant="subtitle1" gutterBottom sx={{ fontWeight: 600 }}>
              Detected Notes ({detectedNotes.length})
            </Typography>
            <Box
              sx={{
                height: 60,
                border: '1px solid',
                borderColor: isDark ? 'rgba(255,107,53,0.2)' : 'rgba(123,45,142,0.15)',
                borderRadius: 2,
                p: 1,
                overflow: 'hidden',
                position: 'relative',
                backgroundColor: isDark ? 'rgba(13,27,42,0.5)' : 'rgba(255,248,240,0.5)',
              }}
            >
              {/* Progress indicator */}
              <Box
                sx={{
                  position: 'absolute',
                  left: 0,
                  top: 0,
                  bottom: 0,
                  width: `${(currentTime / duration) * 100}%`,
                  background: `linear-gradient(90deg, ${theme.palette.primary.main}20 0%, transparent 100%)`,
                  transition: 'width 0.1s linear',
                }}
              />
              
              {detectedNotes.map((note, index) => {
                const position = (note.timestamp / duration) * 100;
                const width = Math.max(2, (100 / detectedNotes.length) * 0.5);
                const noteColor = getSwaraColor(note.swara.symbol);
                
                return (
                  <Box
                    key={index}
                    sx={{
                      position: 'absolute',
                      left: `${position}%`,
                      top: '50%',
                      transform: 'translateY(-50%)',
                      width: `${width}%`,
                      minWidth: 24,
                      height: '80%',
                      background: `linear-gradient(135deg, ${noteColor} 0%, ${noteColor}CC 100%)`,
                      borderRadius: 1,
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      fontSize: '0.65rem',
                      color: 'white',
                      fontWeight: 'bold',
                      boxShadow: `0 2px 8px ${noteColor}40`,
                      transition: 'all 0.2s ease',
                      '&:hover': {
                        transform: 'translateY(-50%) scale(1.1)',
                        zIndex: 10,
                        boxShadow: `0 4px 12px ${noteColor}60`,
                      },
                    }}
                    title={`${note.swara.symbol} - ${formatTime(note.timestamp / 1000)}`}
                  >
                    {note.swara.symbol}
                  </Box>
                );
              })}
            </Box>
          </Box>
        </>
      ) : (
        <Box 
          textAlign="center" 
          py={5}
          sx={{
            background: isDark 
              ? 'radial-gradient(circle at center, rgba(255,107,53,0.05) 0%, transparent 70%)'
              : 'radial-gradient(circle at center, rgba(123,45,142,0.05) 0%, transparent 70%)',
            borderRadius: 2,
          }}
        >
          <Box
            sx={{
              width: 60,
              height: 60,
              borderRadius: '50%',
              background: `linear-gradient(135deg, ${theme.palette.primary.main}30 0%, ${theme.palette.secondary.main}30 100%)`,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              margin: '0 auto',
              mb: 2,
              animation: 'float 3s ease-in-out infinite',
              '@keyframes float': {
                '0%, 100%': { transform: 'translateY(0px)' },
                '50%': { transform: 'translateY(-5px)' },
              },
            }}
          >
            <PlayArrow sx={{ fontSize: 28, opacity: 0.6 }} />
          </Box>
          <Typography variant="body1" color="text.secondary" sx={{ fontWeight: 500 }}>
            Upload an audio file to see the timeline analysis.
          </Typography>
        </Box>
      )}
    </Paper>
  );
};

export default TimelineVisualization;
