import React from 'react';
import {
  Fab,
  Tooltip,
  Zoom,
  Box,
  useTheme,
} from '@mui/material';
import {
  Mic,
  MicOff,
} from '@mui/icons-material';

interface FloatingMicrophoneButtonProps {
  hasPermission: boolean;
  isRecording: boolean;
  onRequestPermission: () => void;
  onStartRecording: () => void;
  onStopRecording: () => void;
  disabled?: boolean;
}

const FloatingMicrophoneButton: React.FC<FloatingMicrophoneButtonProps> = ({
  hasPermission,
  isRecording,
  onRequestPermission,
  onStartRecording,
  onStopRecording,
  disabled = false,
}) => {
  const theme = useTheme();
  const isDark = theme.palette.mode === 'dark';

  const handleClick = () => {
    if (!hasPermission) {
      onRequestPermission();
    } else if (isRecording) {
      onStopRecording();
    } else {
      onStartRecording();
    }
  };

  const getTooltipText = () => {
    if (!hasPermission) {
      return 'Allow Microphone Access';
    } else if (isRecording) {
      return 'Stop Recording';
    } else {
      return 'Start Recording';
    }
  };

  const getIcon = () => {
    if (!hasPermission) {
      return <MicOff />;
    } else if (isRecording) {
      return <MicOff />;
    } else {
      return <Mic />;
    }
  };

  return (
    <Zoom in={true}>
      <Box
        sx={{
          position: 'fixed',
          bottom: 24,
          right: 24,
          zIndex: 1000,
        }}
      >
        {/* Concentric rings animation when recording */}
        {isRecording && (
          <>
            <Box
              sx={{
                position: 'absolute',
                top: '50%',
                left: '50%',
                width: 56,
                height: 56,
                borderRadius: '50%',
                border: '2px solid',
                borderColor: theme.palette.error.main,
                transform: 'translate(-50%, -50%)',
                animation: 'concentricRing 1.5s ease-out infinite',
                '@keyframes concentricRing': {
                  '0%': {
                    transform: 'translate(-50%, -50%) scale(1)',
                    opacity: 0.8,
                  },
                  '100%': {
                    transform: 'translate(-50%, -50%) scale(2.5)',
                    opacity: 0,
                  },
                },
              }}
            />
            <Box
              sx={{
                position: 'absolute',
                top: '50%',
                left: '50%',
                width: 56,
                height: 56,
                borderRadius: '50%',
                border: '2px solid',
                borderColor: theme.palette.error.main,
                transform: 'translate(-50%, -50%)',
                animation: 'concentricRing 1.5s ease-out infinite 0.5s',
              }}
            />
            <Box
              sx={{
                position: 'absolute',
                top: '50%',
                left: '50%',
                width: 56,
                height: 56,
                borderRadius: '50%',
                border: '2px solid',
                borderColor: theme.palette.error.main,
                transform: 'translate(-50%, -50%)',
                animation: 'concentricRing 1.5s ease-out infinite 1s',
              }}
            />
          </>
        )}
        
        <Tooltip title={getTooltipText()} placement="left">
          <Fab
            aria-label="microphone"
            onClick={handleClick}
            disabled={disabled}
            sx={{
              background: !hasPermission
                ? isDark 
                  ? 'linear-gradient(135deg, #4a4a6a 0%, #2a2a4a 100%)'
                  : 'linear-gradient(135deg, #9e9e9e 0%, #757575 100%)'
                : isRecording
                  ? `linear-gradient(135deg, ${theme.palette.error.main} 0%, ${theme.palette.error.dark} 100%)`
                  : `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
              color: 'white',
              boxShadow: isRecording
                ? `0 4px 20px ${theme.palette.error.main}50, 0 0 40px ${theme.palette.error.main}30`
                : `0 4px 20px ${theme.palette.primary.main}50`,
              transition: 'all 0.3s ease-in-out',
              '&:hover': {
                background: !hasPermission
                  ? isDark 
                    ? 'linear-gradient(135deg, #5a5a7a 0%, #3a3a5a 100%)'
                    : 'linear-gradient(135deg, #bdbdbd 0%, #9e9e9e 100%)'
                  : isRecording
                    ? `linear-gradient(135deg, ${theme.palette.error.light} 0%, ${theme.palette.error.main} 100%)`
                    : `linear-gradient(135deg, ${theme.palette.primary.light} 0%, ${theme.palette.secondary.light} 100%)`,
                boxShadow: isRecording
                  ? `0 6px 30px ${theme.palette.error.main}60, 0 0 50px ${theme.palette.error.main}40`
                  : `0 6px 30px ${theme.palette.primary.main}60`,
                transform: 'scale(1.05)',
              },
              '&:active': {
                transform: 'scale(0.98)',
              },
              '&.Mui-disabled': {
                background: isDark 
                  ? 'linear-gradient(135deg, #3a3a5a 0%, #2a2a4a 100%)'
                  : 'linear-gradient(135deg, #e0e0e0 0%, #bdbdbd 100%)',
                color: 'rgba(255,255,255,0.5)',
              },
              // Pulsing animation when recording
              ...(isRecording && {
                animation: 'fabPulse 1s ease-in-out infinite',
                '@keyframes fabPulse': {
                  '0%, 100%': {
                    transform: 'scale(1)',
                  },
                  '50%': {
                    transform: 'scale(1.08)',
                  },
                },
              }),
            }}
          >
            {getIcon()}
          </Fab>
        </Tooltip>
      </Box>
    </Zoom>
  );
};

export default FloatingMicrophoneButton;
