import React from 'react';
import {
  Box,
  Paper,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Chip,
  Alert,
  useTheme,
} from '@mui/material';
import { NOTE_FREQUENCIES } from '../utils/audioProcessing';

interface TonicSelectorProps {
  selectedTonic: string;
  detectedTonic: string | null;
  onTonicChange: (tonic: string) => void;
  isAutoDetecting: boolean;
}

const TonicSelector: React.FC<TonicSelectorProps> = ({
  selectedTonic,
  detectedTonic,
  onTonicChange,
  isAutoDetecting,
}) => {
  const theme = useTheme();
  const isDark = theme.palette.mode === 'dark';

  const noteOptions = Object.keys(NOTE_FREQUENCIES).map(note => ({
    value: note,
    label: note,
    frequency: NOTE_FREQUENCIES[note],
  }));

  return (
    <Paper 
      elevation={0} 
      sx={{ 
        p: 3,
        background: isDark 
          ? 'linear-gradient(145deg, rgba(27,38,59,0.9) 0%, rgba(13,27,42,0.95) 100%)'
          : 'linear-gradient(145deg, rgba(255,255,255,0.95) 0%, rgba(255,248,240,0.9) 100%)',
        backdropFilter: 'blur(10px)',
        border: '1px solid',
        borderColor: isDark ? 'rgba(255,107,53,0.2)' : 'rgba(123,45,142,0.15)',
        borderRadius: 3,
        height: '100%',
      }}
    >
      <Typography 
        variant="h6" 
        gutterBottom
        sx={{
          background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
          backgroundClip: 'text',
          WebkitBackgroundClip: 'text',
          WebkitTextFillColor: 'transparent',
          fontWeight: 600,
        }}
      >
        Tonic Selection
      </Typography>
      
      <Typography variant="body2" color="text.secondary" paragraph sx={{ opacity: 0.8 }}>
        The tonic (Sa) is the fundamental note that all other swaras are measured against.
      </Typography>

      {detectedTonic && (
        <Alert 
          severity="info" 
          sx={{ 
            mb: 2,
            background: isDark 
              ? 'linear-gradient(135deg, rgba(255,179,71,0.15) 0%, rgba(255,107,53,0.1) 100%)'
              : 'linear-gradient(135deg, rgba(255,179,71,0.2) 0%, rgba(255,107,53,0.15) 100%)',
            border: '1px solid',
            borderColor: 'warning.main',
            borderRadius: 2,
            '& .MuiAlert-icon': {
              color: 'warning.main',
            },
          }}
        >
          Auto-detected tonic: <strong>{detectedTonic}</strong> ({NOTE_FREQUENCIES[detectedTonic]} Hz)
        </Alert>
      )}

      <FormControl fullWidth>
        <InputLabel 
          id="tonic-select-label"
          sx={{
            '&.Mui-focused': {
              color: theme.palette.primary.main,
            },
          }}
        >
          Select Tonic
        </InputLabel>
        <Select
          labelId="tonic-select-label"
          value={selectedTonic}
          label="Select Tonic"
          onChange={(e) => onTonicChange(e.target.value)}
          disabled={isAutoDetecting}
          sx={{
            borderRadius: 2,
            '& .MuiOutlinedInput-notchedOutline': {
              borderColor: isDark ? 'rgba(255,107,53,0.3)' : 'rgba(123,45,142,0.2)',
            },
            '&:hover .MuiOutlinedInput-notchedOutline': {
              borderColor: theme.palette.primary.main,
            },
            '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
              borderColor: theme.palette.primary.main,
            },
          }}
        >
          {noteOptions.map((note) => (
            <MenuItem key={note.value} value={note.value}>
              <Box display="flex" justifyContent="space-between" width="100%" alignItems="center">
                <Typography 
                  sx={{ 
                    fontWeight: selectedTonic === note.value ? 700 : 500,
                    color: selectedTonic === note.value ? theme.palette.primary.main : 'inherit',
                  }}
                >
                  {note.label}
                </Typography>
                <Chip
                  label={`${note.frequency} Hz`}
                  size="small"
                  sx={{
                    backgroundColor: selectedTonic === note.value 
                      ? `${theme.palette.primary.main}20`
                      : isDark ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.05)',
                    color: selectedTonic === note.value 
                      ? theme.palette.primary.main 
                      : 'text.secondary',
                    fontWeight: 500,
                    fontSize: '0.7rem',
                  }}
                />
              </Box>
            </MenuItem>
          ))}
        </Select>
      </FormControl>

      {selectedTonic && (
        <Box 
          mt={2} 
          p={2}
          sx={{
            background: `linear-gradient(135deg, ${theme.palette.primary.main}15 0%, ${theme.palette.secondary.main}15 100%)`,
            borderRadius: 2,
            border: '1px solid',
            borderColor: `${theme.palette.primary.main}30`,
            textAlign: 'center',
          }}
        >
          <Typography 
            variant="h4" 
            sx={{ 
              fontFamily: '"Playfair Display", serif',
              fontWeight: 700,
              background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
              backgroundClip: 'text',
              WebkitBackgroundClip: 'text',
              WebkitTextFillColor: 'transparent',
              mb: 0.5,
            }}
          >
            {selectedTonic}
          </Typography>
          <Typography variant="caption" color="text.secondary" sx={{ fontWeight: 500 }}>
            {NOTE_FREQUENCIES[selectedTonic]} Hz
          </Typography>
        </Box>
      )}

      {isAutoDetecting && (
        <Box 
          mt={2}
          sx={{
            display: 'flex',
            alignItems: 'center',
            gap: 1,
            color: theme.palette.primary.main,
          }}
        >
          <Box
            sx={{
              width: 8,
              height: 8,
              borderRadius: '50%',
              backgroundColor: theme.palette.primary.main,
              animation: 'pulse 1.5s ease-in-out infinite',
            }}
          />
          <Typography variant="body2" sx={{ fontWeight: 500 }}>
            Auto-detecting tonic from audio...
          </Typography>
        </Box>
      )}
    </Paper>
  );
};

export default TonicSelector;
